import request from "@/utils/request";
export default {
 data() {
    return {
      photo: {},
      photoList: {},
    };
  },
  methods: {
  },
  async mounted() {
    let id = this.$route.params.id;
    console.log(this.$route);
    await request.get("/album/" + id).then((res) => {
      console.log(res);
      console.log(id);
      this.photo = res.data.data;
      this.photoList = res.data.data.photosList;
    });

  },
};
